.home {
	width: 100%;
	height: 90vh;
	display: flex;
	justify-content: center;
	align-items: left;
	flex-direction: column;
	background-image: url("../assets/pizza.jpeg");
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  
  .headerContainer {
	width: auto;
	margin-left: 50px;
  }
  
  .headerContainer h1 {
	justify-content: center;
	align-items: center;
	font-size: 45px;
	height: 185px;
	font-weight: 50;
	color: rgb(73, 75, 60);
  }
  
  .headerContainer p {
	padding: 0;
	font-size: 40px;
	font-weight: lighter;
	color: rgb(3, 3, 0);
  }
  
  .headerContainer button {
	color: black;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	justify-content: center;
	background-color: #121619;
	height: 50px;
	width: 180px;
	color: white;
	text-decoration: none;
	font-size: 20px;
  }
  
  .headerContainer button:hover {
	background-color: #07090a;
	cursor: pointer;
	transition: 0.3s ease-in;
  }
  
  @media only screen and (max-width: 650px) {
	.home {
	  justify-content: center;
	  align-items: center;
	}
  
	.headerContainer {
	  margin-left: 0px;
	  border-radius: 10px;
	  padding: 10px;
	  width: 80%;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  flex-direction: column;
	  background-color: #121619;
	  color: white;
	}
  
	.headerContainer h1 {
	  font-size: 40px;
	  height: 30px;
	  color: white;
	}
  
	.headerContainer p {
	  font-size: 30px;
	  color: white;
	}
  
	.headerContainer button {
	  background-color: white;
	  color: #121619;
	  margin-bottom: 30px;
	}
  
	.headerContainer button:hover {
	  background-color: rgb(225, 225, 225);
	  color: #121619;
	}
  }